

.carousel {
	width: 100%;
	overflow: hidden;
	padding: 30px;
	margin-top: 7rem;
	position: relative;
}

.carousel__container {
	
	flex-wrap: wrap;
	display: flex;
	align-items: center;
	justify-content: center;
	margin: 70px 0px;
	padding-bottom: 10px;

}

.carousel h1{
	max-width: 100vw;
	line-height: 1;
}
.carousel-item {
	background-color: palevioletred;
	width: 350px;
	height: 200px;
	border-radius: 20px;
	overflow: hidden;
	margin-right: 30px;
	margin-bottom: 30px;
	display: inline-block;
	cursor: pointer;
	transition: 450ms all;
	transform-origin: center left;
	position: relative;

}

.carousel-item:hover~.carousel-item {
	transform: translate3d(0, 0, 0);
}

.carousel__container:hover .carousel-item {
	opacity: 0.3;
}

.carousel__container:hover .carousel-item:hover {
	transform: scale(1.3);
	opacity: 2;
}

.carousel-item__img {
	width: 400px;
	height: 250px;
	object-fit: cover;
}

.carousel-item__details {
	background: linear-gradient(to top, rgba(0, 0, 0, 0.9)0%, rgba(0, 0, 0, 0)100%);
	font-size: 10px;
	opacity: 0;
	transition: 450ms opacity;
	padding: 10px 10px;
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;

	display: flex;
	justify-content: flex-end;
	flex-direction: column;

}
.carousel-item__details img{
height: 15px;
}
.carousel-item__details p{
	margin: 0px;
}

.carousel-item__details:hover {
	opacity: 1;
	color: white;


}


@media only screen and (max-width: 600px){
    .register__container{
        background-color: transparent;
        border: none;
        padding: 0px;
        width: 100%;
    }
    .footer{
        align-items: flex-start;
        flex-direction: column;
    }
}